.slider {
  position: relative; }
  .slider__slide {
    min-height: 100px; }
  .slider__arrow {
    position: absolute;
    z-index: 1;
    outline: none;
    top: 0;
    bottom: 0;
    padding: 20px;
    background: none;
    border: none;
    color: #ffcc00;
    font-size: 2em;
    transition: all .4s ease-in;
    transition: all .4s ease-in; }
    .slider__arrow:hover, .slider__arrow:focus, .slider__arrow:active {
      transform: scale(1.2);
      cursor: pointer; }
    .slider__arrow--prev {
      left: 0; }
      .slider__arrow--prev i {
        margin-left: -3px; }
    .slider__arrow--next {
      right: 0; }
      .slider__arrow--next i {
        margin-right: -3px; }
  .slider__dots {
    padding: 0;
    margin: 0;
    list-style: none;
    text-align: center; }
    .slider__dots li {
      display: inline-block; }
  .slider__dot {
    display: inline-block;
    width: 16px;
    height: 16px;
    margin: 0 5px;
    border-radius: 50%;
    background-color: #ffcc00;
    border: 1px solid #fff;
    transition: all .4s ease-in;
    transition: all .4s ease-in; }
    .slider__dot:hover, .slider__dot:focus, .slider__dot:active, .slider__dot--active {
      background-color: #fff;
      border-color: #ffcc00;
      cursor: pointer; }

.social {
  list-style: none;
  padding: 0;
  text-align: center; }
  .social__item {
    display: inline-block;
    margin: 5px; }
  .social__link {
    display: inline-block;
    color: #cccccc;
    text-decoration: none;
    width: 32px;
    height: 32px;
    line-height: calc(32px - 2 * 2px);
    border: 2px solid #cccccc;
    border-radius: 50%;
    transition: all .4s ease-in;
    transition: all .4s ease-in; }
    .social__link:hover, .social__link:focus, .social__link:active {
      background-color: #cccccc;
      color: #fff; }

* {
  box-sizing: border-box; }

html {
  font-size: 10px; }

body {
  font-family: "Raleway", sans-serif;
  font-size: 1.4rem;
  color: #333333;
  font-weight: 400;
  line-height: 1.7;
  padding: 50px 0;
  background-color: #fff; }

.container {
  margin: 0 auto;
  max-width: 1170px;
  padding: 15px; }

.head {
  text-align: center;
  text-transform: uppercase;
  margin-bottom: 50px; }
  .head__subtitle {
    font-family: "Raleway", sans-serif;
    font-size: 1.8rem;
    color: #898f8f;
    font-weight: 500;
    line-height: 1.3; }
  .head__title {
    font-family: "Roboto", sans-serif;
    font-size: 6rem;
    color: #ffcc00;
    font-weight: 900;
    line-height: 1.1;
    margin-top: 0; }
  .head__icon svg {
    fill: #ffcc00;
    margin: 0 10px; }
  .head__icon:before, .head__icon:after {
    content: "";
    display: inline-block;
    width: 210px;
    border-top: 1px solid #ffcc00;
    vertical-align: middle;
    margin-top: -10px; }

.profile {
  display: flex; }
  .profile__section {
    width: 50%;
    padding: 80px;
    text-align: center;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center; }
    .profile__section + .profile__section {
      border-left: 1px solid #ffcc00; }
  .profile__img-wrap {
    border: 1px solid #ffcc00;
    font-size: 0;
    padding: 15px;
    position: relative; }
    .profile__img-wrap:before, .profile__img-wrap:after {
      content: "";
      width: calc(100% + 1px *2);
      height: 50%;
      background-color: #fff;
      position: absolute;
      top: 50%;
      left: 50%;
      transform: translate(-50%, -50%); }
    .profile__img-wrap:after {
      height: calc(100% + 1px *2);
      width: 50%; }
  .profile__img {
    border: 1px solid #ffcc00;
    width: 100%;
    position: relative;
    z-index: 1; }
  .profile__title {
    font-family: "Roboto", sans-serif;
    font-size: 3.6rem;
    color: #666666;
    font-weight: 900;
    line-height: 1; }
    .profile__title:after {
      content: "";
      display: block;
      width: 100px;
      border-top: 1px solid #ffcc00;
      margin: 30px auto 0; }
  .profile__subtitle {
    font-size: 2.4rem;
    color: #666666;
    margin-top: 30px; }
  .profile__text {
    margin-bottom: 45px; }
    .profile__text:after {
      content: '';
      display: block;
      width: 50px;
      height: 4px;
      margin: 25px auto 0;
      background-image: linear-gradient(to right, #ffcc00 0%, #ffcc00 20%, #fff 20%, #fff 30%, #ffcc00 30%, #ffcc00 70%, #fff 70%, #fff 80%, #ffcc00 80%); }
  .profile__link {
    text-transform: uppercase;
    font-family: "Open Sans", sans-serif;
    font-size: 1.4rem;
    color: #666666;
    font-weight: 700;
    line-height: 1;
    text-decoration: none;
    border: 1px solid #ffcc00;
    display: inline-block;
    padding: .5em 1.4em;
    transition: all .4s ease-in;
    transition: all .4s ease-in; }
    .profile__link:hover, .profile__link:focus, .profile__link:active {
      background-color: #ffcc00;
      color: #fff; }
